	export default {
		data() {
			return {
				topInfo: {},
				list: [],
				page: 1
			}
		},
		onLoad(){
			this.api({
				method: "get",
				url: "Order/order_bonus_info",
				data: {
					page: 1
				}
			}).then(res=> {
				console.log(res);
				this.topInfo = res.data.date.one_bonus_info;
				this.list = res.data.date.list;
			})
		},
		onShow() {

		},
		onReachBottom() {
			this.init()
		},
		methods: {
			init() {
				this.page++;
				this.api({
					method: "get",
					url: "Order/order_bonus_info",
					data: {
						page: this.page
					}
				}).then(res=> {
					console.log(res);
					res.data.date.list.forEach(item=> {
						this.list.push(item)
					})
				})
			}
		}
 	}
